video {
  object-fit: cover;
  width: 100%;
  height: 80%;
  position: absolute;
  
}
.hero-container {
    /* background: url('./Images/img-1.jpg') center center/cover no-repeat;  */
    height: 70vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }

  .hero-container > h1 {
    position:absolute;
    color: #fff;
    font-size: 8vh;
    margin-top: -5vh;
    margin-bottom: 2vh;
    margin-left: 10vh;
    margin-right: 5vh;
    
  }
  .hero-container > h2 {
    position:absolute;
    color: #fff;
    font-size: 10vh;
    margin-top: -40vh;
    margin-left: 10vh;
    margin-right: 5vh;
    margin-bottom: 2vh;
  
    
  }
  
  .hero-container > h3 {
    position:absolute;
    color: #fff;
    font-size: 5vh;
    margin-top: 20vh;
    
  }
  .img {
    position: relative;
  }
  /* .hero-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  } */

  @media screen and (max-width: 960px) {
    .hero-container > h1 {
      font-size:large;
      margin-top: -5vh;
    }
    .hero-container > h2 {
      font-size:large;
      margin-top: -40vh;
    }
    .hero-container > h3 {
      font-size:large;
      margin-top: -1vh;
    }
  }
  
  @media screen and (max-width: 768px) {
    .hero-container > h1 {
      font-size:large;
      margin-top: -10vh;
    }
    .hero-container > h2 {
      font-size:large;
      margin-top: -40vh;
    }
    .hero-container > h3 {
      font-size:large;
      margin-top: -1vh;
    }
    }